
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import orderClassify from '@/components/orderClassify.vue'
import { getProductListOne } from '@/api/goods.js'
import nodata from '@/components/noData'

export default {
name: 'homeIndex',
components: {
Group,
XInput,
orderClassify,
nodata
},
data () {
return {
resShow: false,
visible: false,
top: 0,
left: 0,
selectedTag: {},
date: '2018-08-17',
show: false,
skulist: [],
postData: {
name: null,
sortorder: '',
page: 1,
pagenum: 50
},
searchword:''
}
},
created () {


this.searchword = window.localStorage.getItem('searchword')

},
methods: {

handleGetList () {
if (!this.postData.name) {
this.$vux.toast.text('请输入搜索关键词', 'middle')
this.resShow = false
return
}
this.$vux.loading.show()
this.resShow = true
getProductListOne(this.postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.skulist = res.data.data.skulist
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
console.log(res)
}
})
.catch(err => {
this.$vux.loading.hide()
this.$vux.toast.text('网络异常！', 'middle')
console.log(err)
})
},

handleSort (item) {
if (item === 'price') {

if (this.postData.sortorder === 'saledesc' || this.postData.sortorder === 'saleasc' || this.postData.sortorder === '') {

console.log(1212)
this.postData.sortorder = 'priceasc'
} else {
this.postData.sortorder = this.postData.sortorder === 'pricedesc' ? 'priceasc' : 'pricedesc'
}
} else if (item === 'sale') {

if (this.postData.sortorder === 'pricedesc' || this.postData.sortorder === 'priceasc' | this.postData.sortorder === '') {

this.postData.sortorder = 'saleasc'
} else {
this.postData.sortorder = this.postData.sortorder === 'saledesc' ? 'saleasc' : 'saledesc'
}
}

console.log(this.postData)
this.handleGetList()
}
}
}



